/* 初始化 */
html,body{
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;

    font-family: Arial, Helvetica, sans-serif;
}
ul{
    margin: 0;
    list-style: none;
}
a{
    text-decoration: none;
}
/* 元件部分 */
.clearfix:after{
	content: " ";
	display: block;
	height: 0;
	line-height: 0;
	visibility: hidden;
	clear: both;
}
.clearfix{
	zoom:1;/*兼容IE浏览器写法*/
}
/* 左浮动 */
.fl {
    float: left;
}
/* 右浮动 */
.fr {
    float: right;
}
/* 左右弹性布局 */
.flex-display {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    justify-items: center;
}
.border-bottom {
    border-bottom: 1px solid #ccc;
}
.LH-h3 {
    font-family: Arial, Helvetica, sans-serif;
    word-spacing: 3px;
    font-size: 17px;
}
.LH-p {
    line-height: 24px;
    font-family: Arial, Helvetica, sans-serif;
    word-spacing: 3px;
    font-size: 14px;
    margin: 0;
}
.LH-h4 {
    line-height: 24px;
    margin: 30px 0 0 0;
    font-family: Arial, Helvetica, sans-serif;
    word-spacing: 3px;
    font-size: 14px;
}